<template>
  <Modal
    :active="showModal"
    :event-name="'follow-up-full-view-modal'"
    :full-screen="'nexus'"
    :custom-width="'w-full ipad:w-[768px] ipadPro:w-[1024px] lg:w-[1240px]'"
    :custom-horizontal-padding="'px-spacing-0'"
    :custom-vertical-padding="'pt-spacing-500'"
  >
    <IconX
      @click.native="handleClose()"
      class="absolute right-spacing-300 top-spacing-300 h-spacing-300 w-spacing-300 cursor-pointer text-content-on-neutral-secondary hover:text-content-on-neutral-primary"
    />
    <FollowUpFullView
      v-if="marketResearchData && companyData"
      :market-research-data="marketResearchData"
      :company-data="companyData"
      class="overflow-auto nexus:max-h-[85dvh]"
    />
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    FollowUpFullView: () =>
      import("@/components/MarketResearch/FollowUpFullView.vue"),
    IconX: () => import("@/components/svgIcons/x.vue"),
  },
  data() {
    return {
      showModal: false,
      companyData: null,
      marketResearchData: null,
    };
  },
  mounted() {
    this.$bus.$on("follow-up-full-view-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.companyData = data?.companyData;
        this.marketResearchData = data?.marketResearchData;
      } else {
        this.showModal = false;
        this.companyData = null;
        this.marketResearchData = null;
      }
    });
  },
  methods: {
    handleClose() {
      this.$bus.$emit("follow-up-full-view-modal", false);
    },
  },
  beforeDestroy() {
    this.$bus.$off("follow-up-full-view-modal");
  },
};
</script>
